<template>
  <div>
    <!-- 卡片 -->
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>商品分类</span>

        <el-button @click="dialogFormVisible = true" type="primary">添加分类</el-button>
      </div>
      <!-- 添加分类 -->
      <el-dialog title="添加分类" :visible.sync="dialogFormVisible">
        <el-form :model="form">
          <el-form-item label="分类名称" :label-width="formLabelWidth">
            <el-input v-model="form.name" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="分类名称" :label-width="formLabelWidth">
            <template>
              <el-switch v-model="form.value" active-color="#13ce66" inactive-color="#ff4949"></el-switch>
            </template>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="addshopclassify">确 定</el-button>
        </div>
      </el-dialog>
      <!-- 商品分类数据 -->
      <div>
        <el-table :data="tableData" style="width: 100%">
          <el-table-column label="序号" width="180">
            <template slot-scope="scope">
              <span style="margin-left: 10px">{{ scope.row.id }}</span>
            </template>
          </el-table-column>
          <el-table-column label="分类名称" width="180">
            <template slot-scope="scope">
              <span style="margin-left: 10px">{{ scope.row.cateName }}</span>
            </template>
          </el-table-column>
          <el-table-column label="是否启用" width="180">
            <template slot-scope="scope">
              <el-switch :disabled="scope.row.test" v-model="scope.row.state" active-color="#13ce66" inactive-color="#ff4949"></el-switch>
            </template>
          </el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
              <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="[5, 10, 15, 20]"
          :page-size="5"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        ></el-pagination>
      </div>
    </el-card>
  </div>
</template>

<script>
// 引入接口
import {
  API_GET_SHOP_CLASSIFY,
  API_ADD_SHOP_CLASSIFY,
  API_DELETE_SHOP_CLASSIFY
} from "@/API/apis.js";

export default {
  data() {
    return {
      
      // 商品分类启用状态
     switchStatus:true,
      // 商品分类列表
      tableData: [],
      currentPage: 1, //当前激活页
      pageSize: 5, //每页显示条数
      total: 0, //商品分类数据总条数
      dialogFormVisible: false, //模态框显示/隐藏切换
      form: {
        //模态框内嵌套的表单数据
        name: "", //分类名
        value: true //启用状态
      },
      formLabelWidth: "120px" //表单内容宽度
    };
  },
  methods: {
    // 编辑分类
    handleEdit(index,row){
      console.log(index,row) //index 当前点击索引  row 当前元素
      row.test=false      

      console.log(row.test);
      
      // this.tableData.forEach(item=>{
      //  item.switchStatus
        
      // })
      // this.off=!this.off
    },
    // 删除所选分类
    handleDelete(index, row) {
      index;
      // 商品ID row.id
      // 弹框确认是否删除
      this.$confirm("此操作将删除该商品, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          // 发送请求,删除商品分类
          API_DELETE_SHOP_CLASSIFY(row.id).then(res => {
            //console.log(res);
            if (res) {
              this.getShopClassify(); //调用获取商品分类函数
              this.$message({
                type: "success",
                message: "删除成功!",
                duration: 1500
              });
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
            duration: 1500
          });
        });
    },
    // 改变每页显示条数
    handleSizeChange(val) {
      this.pageSize = val;
      this.getShopClassify(); //调用获取商品分类函数
      // console.log(this.pageSize);
    },
    // 改变当前页
    handleCurrentChange(val) {
      this.currentPage = val;
      this.getShopClassify(); //调用获取商品分类函数
      //console.log(this.currentPage = val);
    },
    // 封装获取商品分类函数
    getShopClassify() {
      API_GET_SHOP_CLASSIFY(this.currentPage, this.pageSize).then(res => {
        if (res.statusText == "OK") {
          //如果请求成功
          this.total = res.data.total; // 获取数据条数
          this.tableData = res.data.data; //获取表格数据
          // 遍历数据
          this.tableData.forEach(item => {
            item.state = Boolean(item.state); //把每一项的state 1  0 转为布尔值
          });
        }
      });
    },
    // 添加商品分类
    addshopclassify() {
      //console.log(this.form.name,this.form.value)
      // 发送请求添加分类 注：布尔类型数值转化为字符串后再发送
      API_ADD_SHOP_CLASSIFY(
        this.form.name,
        JSON.stringify(this.form.value)
      ).then(res => {
        ///console.log(res)
        if (res.data.code == 0) {
          this.$message({
            message: res.data.msg, //提示内容
            type: "success",
            duration: 1500 //消息提示 显示1.5秒
          });
          this.form.name = ""; //添加成功后初始化内容
          this.form.value = true; //初始化内容
          this.dialogFormVisible = false; //隐藏模态框
          this.getShopClassify(); //重新获取商品分类
        }
      });
    }
  },
  created() {
    // 组件，data初始化完毕后，获取列表数据
    this.getShopClassify();
  }
};
</script>

<style lang="less" scoped>
.clearfix {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
</style>